.outro {
    margin-top: 100px;
    &__viewable {
        max-width: 520px;
        margin: auto;
        &Title {

            font-size: 15px;
            font-weight: 500;
            background: transparent;
            border: none;
            position: relative;
            &::after {
                z-index: 10;
                content: '';
                height: 29px;
                width: 29px;
                position: absolute;
                left: -40px;
                top: 5px;
                background-image: url(../../img/footer/footer__icon.png);
                background-repeat: no-repeat; } }
