.header {
    position: relative;
    height: 188px;
    display: flex;
    justify-content: center;
    flex: 0 1 33.333%;
    flex-wrap: wrap;
    align-items: center;
    background: #f1f1f1;
    &__menu {
        display: flex;
        &Link {
            font-weight: 500;
            font-size: 12px;
            color: #000; }
        &Item {
            margin-left: 60px; } }
    &__contacts {
        margin-left: 80px;
        font-size: 12px;
        display: flex;
        &Phone {
            line-height: 1.2;
            margin-left: 60px;
            &Text {
                font-size: 17px;
                font-weight: 900;
                text-align: center; }
            &Freecall {
                color: #4ca957;
                text-transform: uppercase; } }
        &Address {
            line-height: 1.2;
            text-align: right; } } }

@media screen and (max-width: 767px) {
    .header {
        &__logo {
            &Img {
                width: 185px;
                height: 70px; } }
        &__nav {
            display: none; }
        &__contacts {
            position: absolute;
            top: 200px;
            margin-left: -24px;
            margin-top: 20px;
            justify-content: center;
            &Address {
                display: none; } } } }

@media screen and (max-width: 1200px) {
    .header {
        .container {
            // flex-direction: column
            justify-content: center;
            align-items: center; }
        &__logo {
 }            // right: 50px
        &__menu {
            width: 100%;
            flex-direction: column;
            justify-content: center; }
        &__contacts {

            &Address {
                text-align: center; } } } }
