.feedback {
    position: relative;
    &__row {
        margin-top: 100px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; }
    &__review {
        position: relative;
        max-width: 800px;
        margin-left: 50px;
        line-height: 0.7;
        &Name {
            font-family: 'Quacker';
            font-weight: 400;
            font-size: 53px;
            text-align: left; }
        &Town {
            font-size: 15px;
            font-weight: 700;
            text-transform: uppercase; }
        &Text {
            font-size: 17px;
            line-height: 1.3; }
        &Expand {
            margin-top: 20px;
            text-align: left;
            font-size: 18px;
            color: #4ac459;
            text-decoration: underline; }
        &Up {
            position: absolute;
            right: -70px;
            top: 20%; }
        &Down {
            position: absolute;
            right: -70px;
            bottom: 20px;
            transform: rotate(180deg); } } }

@media screen and (max-width: 1199px) {
    .feedback {
        &__img {
            margin-left: 25px;
            width: 120px;
            height: 120px; }

        &__review {
            width: 80%;
            margin: auto;
            margin-top: 20px; } } }

@media screen and (max-width: 767px) {
    .feedback {
        &__review {
            &Name {
                display: none;
                position: absolute;
                top: -130px;
                right: 10%; }
            &Town {
                display: none;
                position: absolute;
                top: -85px;
                right: 70px; }
            &Up {
                dn {}
                transform: rotate(270deg);
                top: -65px;
                left: 150px; }
            &Down {
                transform: rotate(90deg);
                top: -65px;
                left: 210px; } } } }
