.steps {
    margin-top: 50px;
    &__title {
        font-family: 'Quacker';
        font-weight: 400;
        font-size: 65px;
        margin-bottom: 50px; }
    &__basicSteps {

        display: flex;
        flex: 0 1 33.333%;
        justify-content: center;
        flex-wrap: wrap;
        &Item {
            width: 400px;
            margin-left: 20px;
            padding: 30px 20px;
            background-image: linear-gradient(#e3e3e3, #eef2f3);
            border-radius: 20px;
            display: flex;
            align-items: center;
            &Title {
                margin-left: 10px;
                text-transform: uppercase;
                font-size: 25px; } } }
    &__advancedSteps {
        margin-top: 20px;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        &Item {
            width: 400px;
            // min-width: 340px
            margin-left: 20px;
            padding: 30px 20px;
            background-image: linear-gradient(#e3e3e3, #eef2f3);
            border-radius: 20px;
            display: flex;
            align-items: center;
            &Title {
                margin-left: 10px;
                text-transform: uppercase;
                font-size: 25px; } } }

    &__form {
        // background: url(../img/steps/steps__form-bg.png) no-repeat
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        &Title {
            font-size: 28px;
            font-weight: 700;
            text-transform: uppercase; }
        &SubTitle {
            font-size: 20px;
            font-weight: 500;
            text-transform: uppercase; }
        &Email {
            border-radius: 5px;
            margin-top: 20px;
            position: relative;
            left: 50%;
            transform: translate(-50%, 0); }
        &Btn {
            width: 200px;
            position: relative;
            left: 50%;
            transform: translate(-50%, 0);
            margin-top: 30px; } }

    @media screen and ( max-width: 767px ) {
        .steps {
            margin-top: 0;
            &__title {
                margin-bottom: 20px;
                font-size: 40px; }
            &__advancedSteps {
                &Item {
                    margin-left: 60px;
                    justify-content: space-around;
                    width: 200px;
                    &Title {
                        font-size: 10px; }
                    &Icon {
                        width: 60px;
                        height: 60px; } } }
            &__basicSteps {
                &Item {
                    margin-left: 60px;
                    justify-content: space-around;
                    width: 200px;
                    &Title {
                        font-size: 10px; }
                    &Icon {
                        width: 60px;
                        height: 60px; } } }
            &__form {
                width: 400px;
                &Title {
                    font-size: 15px; }
                &Subtitle {
                    font-size: 14px; } } }

        .steps.swiper-slide {
            width: 80%;
            height: 50px !important; } } }
