.gallery {
    margin-top: 100px;
    &__row {
        display: flex;
        flex: 0 1 33.333%;
        flex-wrap: wrap;
        justify-content: space-around; }
    &__unselectedImages {
        display: flex;
        flex-direction: column;
        &Item {
            width: 220px;
            height: 140px;
            margin: 0;
            padding: 0; } }
    &__selectedImage {
        width: 920px;
        height: 570px;
        position: relative;
        &Item {
            margin-top: 15px;
            width: 100%;
            height: 558px; } }
    &__selectors {
        cursor: pointer;
        text-transform: uppercase;
        font-size: 20px;
        font-weight: 700;
        &Rock {
            line-height: 70px;
            text-align: center;
            color: #fff;
            height: 70px;
            width: 55%;
            background: #4aca5a;
            position: absolute;
            top: 0; }
        &Wood {
            border-radius: 0 20px 0 20px;
            line-height: 70px;
            text-align: center;
            color: #000;
            height: 70px;
            width: 45%;
            background: #d8d8d8;
            position: absolute;
            top: 0;
            right: 0; } }
    &__desc {
        // display: flex
        margin-top: 60px;
        &Row {
            // margin-top: 20px
            display: flex;

 }            // flex-wrap: wrap
        &Works {
            display: flex;
            flex-direction: column;
            // flex-wrap: wrap
            margin-left: 50px;
            &Title {
                text-align: left;
                font-size: 27px;
                font-weight: 700; }
            &Text {
                margin-left: 10px;
                text-align: left;
                font-size: 18px;
                font-weight: 500; }
            &Deadline {
                margin-left: 110px; } }
        &Btn {
            margin-top: 30px;
            font-size: 25px; } } }

.mobileGallery > {
    height: 150px !important; }
@media screen and (max-width: 1800px) {
    .gallery {
        &__selectedImage {
            width: 736px;
            &Item {
                height: 444px; } }
        &__unselectedImages {
            &Item {
                width: 176px;
                height: 112px; } }
        &__desc {
            &Row {
                flex-direction: column; }
            &Works {
                &Title {
                    font-size: 20px; }
                &Text {
                    font-size: 15px; }
                &Deadline {
                    margin-left: 50px; } }
            &Btn {
                font-size: 20px; } } } }

@media screen and (max-width: 1600px) {
    .gallery {
        &__selectedImage {
            width: 613px;
            height: 500px; } } }

@media screen and (max-width: 767px) {
    .gallery {
        margin-top: 150px;
        &__selectors {

            font-size: 15px;
            &Rock {
                width: 50%;
                line-height: 2;
                max-height: 100;
                top: -152px; }
            &Wood {
                width: 55%;
                line-height: 2;
                max-height: 100;
                top: -152px; } }

        &__selectedImage {
            width: 98%;
            margin: auto;
            &item {
                max-width: 90px;
                height: 70px; } }
        &__unselectedImages {
            max-width: 98%;
            margin: auto;
            height: 50px;
            &Item {
                max-width: 100px;
                height: 80px; } }
        &__desc {
            max-width: 100%;
            margin: auto;
            &Row {
                margin-top: 20px;
                flex-direction: row; }
            &Works {
                &Title {
                    font-size: 10px; }
                &Text {
                    margin: 0;
                    margin-top: 10px;
                    font-size: 10px; }
                &Deadline {
                    margin-left: 120px; } }
            &Btn {
                font-size: 10px; } } } }

@media screen and (max-width: 320px) {
    .gallery {
        &__unselectedImages {
            width: 100%;
            &Item {
                max-width: 90px;
                height: 70px; } }
        &__selectors {
            font-size: 10px; } } }
