.projects {
    max-width: 1170px;
    margin: auto;
    margin-top: 50px;
    &__title {
        font-family: 'Quacker';
        font-weight: 400;
        font-size: 75px; }
    .row {
        margin-top: 30px;
        display: flex; }
    &__sizes {
        max-width: 1000px;
        margin: auto;
        display: flex;
        flex-direction: column;
        // flex: 0 1 20%
        justify-content: space-between;
        flex-wrap: wrap;
        &Item {
            display: inline-block;
            font-size: 35px;
            font-weight: 700;
            text-transform: uppercase;
            position: relative; } }
    &__preview {
        position: relative;
        margin-top: 60px;
        display: flex;
        flex-wrap: wrap;
        &Img {
            cursor: pointer;
            width: 630px;
            height: 350px; }
        &Choose {
            cursor: pointer;
            position: absolute;
            top: 0;
            left: 50px;
            height: 70px;
            width: 120px;
            border: 1px solid #000;
            transform: rotate(4deg);
            &:nth-child(2) {
                left: 45px;
                top: 71px; }
            &:nth-child(3) {
                width: 130px;
                height: 76px;
                top: 166px;
                left: 30px;
                transform: rotate(4deg); }
            &:nth-child(4) {
                width: 130px;
                height: 80px;
                left: 27px;
                top: 262px;
                transform: rotate(5deg); }
            &:nth-child(5) {
                width: 142px;
                height: 85px;
                left: 170px;
                top: 260px;
                transform: none; }
            &:nth-child(6) {
                width: 140px;
                height: 85px;
                left: 338px;
                top: 260px;
                transform: rotate(-3deg); }
            &:nth-child(7) {
                top: 258px;
                left: 490px;
                width: 130px;
                height: 80px;
                transform: rotate(-9deg); }
            &:nth-child(8) {
                width: 130px;
                height: 88px;
                top: 155px;
                left: 483px;
                transform: rotate(-10deg); }
            &:nth-child(9) {
                width: 125px;
                left: 476px;
                top: 70px;
                transform: rotate(-9deg); }
            &:nth-child(10) {
                width: 125px;
                top: 0;
                left: 464px;
                transform: rotate(-9deg); } }
        &Desc {
            margin-left: 70px;
            &Title {
                font-family: 'Quacker';
                font-weight: 400;
                font-size: 65px; }
            &Plans {
                font-size: 20px;
                text-transform: uppercase;
                line-height: 1;
                &Title {
                    font-size: 30px;
                    font-weight: 700; }
                &Basic {
                    margin-top: 30px; } }
            &Btn {
                margin-top: 25px;
                line-height: 1.65;
                width: 240px;
                height: 50px;
                font-size: 15px; } } }
    &__gallery {
        display: none;
        margin-top: 100px;
        &Title {
            font-size: 20px;
            text-transform: uppercase;
            font-weight: 500; }
        &Slider {
            display: flex;
            align-items: center;
            &Prev {
                margin-right: 50px;
                width: 24px;
                height: 40px; }
            &Next {
                margin-left: 50px;
                width: 24px;
                height: 40px; } } } }

@media screen and (max-width: 991px) {
    .projects {
        &__title {
            font-size: 50px; }
        &__sizes {
            &Item {
                font-size: 15px; } }
        &__preview {
            &Desc {
                // width: 100%
                margin: auto;
                &Title {
                    font-size: 50px;
                    text-align: center; } } } } }

@media screen and (max-width: 767px) {
    .projects {
        &__title {
            font-size: 40px; }
        .row {
            display: flex; }
        &__sizes {
            margin-top: 50px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            &Item {
                font-size: 25px; } }
        &__preview {
            &Desc {
                width: 100%;
                margin: auto;
                &Title {
                    display: none; }
                &Plans {
                    font-size: 15px;
                    text-align: center;
                    &Title {
                        font-size: 20px; }
                    &Basic {
                        margin-top: 10px; } } } } } }
